import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { DepositProductPlexiblePricingComponent } from './deposit-product-flexible-pricing.component';
import { AddDepositProductExamComponent } from './add-deposit-product-exam/add-deposit-product-exam.component';

export const routes: Routes = [
    {
        path: '', component: DepositProductPlexiblePricingComponent
    },
    {
        path: 'add-deposit-product-exam', component: AddDepositProductExamComponent
    }
]
export const DepositProductFlexiblePricingRouting: ModuleWithProviders = RouterModule.forChild(routes);