import { Component, OnInit } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'add-deposit-product-exam',
  templateUrl: './add-deposit-product-exam.component.html',
  styleUrls: ['./add-deposit-product-exam.component.scss']
})

export class AddDepositProductExamComponent implements OnInit {

  tabMesg: any = tableMessage;
  msgs: Message[] = [];
  detailList: any;
  tabTable: any[] = [];
  fieldList: any[] = [
    { label: '序号', value: 'seqNo' },
    { label: '客户号', value: 'custId' },
    { label: '客户姓名', value: 'custName' },
    { label: '币种', value: 'currency' },
    { label: '存款账户号', value: 'accountNo' },
    { label: '科目号', value: 'subjectNum' },
    { label: '账户产品类型', value: 'accountProductType' },
    { label: '产品类型', value: 'productType' },
    { label: '存款金额', value: 'depositAmount' },
    { label: '原约定利率', value: 'originalExecuteRate' },
    { label: '浮动加点', value: 'floatPoint' },
    { label: '浮动加比', value: 'floatRatio' },
    { label: '约定日均', value: 'appointAverageDaily' },
    { label: '执行利率', value: 'executeRate' },
    { label: '是否赠送积数比例标识', value: 'isProductRatio' },
    { label: '大客户费用标识', value: 'bigCustMark' },
    { label: '申请理由', value: 'applyReason' },
    { label: '文件url', value: 'filePath' },
    { label: '文件名', value: 'fileName' },
    { label: '状态', value: 'status' },
    { label: '创建人', value: 'creater' },
    { label: '创建人名称', value: 'createrName' },
    { label: '创建人机构', value: 'createrOrg' },
    { label: '创建人机构名称', value: 'createrOrgName' },
    { label: '亲情账户1', value: 'famAccoAcctNo1' },
    { label: '亲情账户2', value: 'famAccoAcctNo2' },
    { label: '亲情账户3', value: 'famAccoAcctNo3' },
    { label: '亲情账户4', value: 'famAccoAcctNo4' },
    { label: '亲情账户5', value: 'famAccoAcctNo5' },
  ];
  fieldHeaderList1: any[];
  fieldHeaderList2: any[];
  flexiblePricing: boolean = true;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public activatedRoute: ActivatedRoute
  ) {

  }

  ngOnInit() {
    // 获取参数
    this.activatedRoute.params.subscribe(params => {
      this.detailList = params;
    })
    if (this.detailList.seqNo) {
      this.queryFlexibleDepositInterest({ seqNo: this.detailList.seqNo });
    }
  }

  queryFlexibleDepositInterest(params) {
    this.httpService.queryFlexibleDepositInterest(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tabTable = data.resultList;
        if (data.resultList && data.resultList.length != 0) {
          this.fieldHeaderList1 = [];
          this.fieldHeaderList2 = [];
          this.fieldList.forEach(element => {
            for (let item in data.resultList[0]) {
              if (element.value == item && data.resultList[0][item]) {
                let objTemp = { label: element.label, value: item };
                if (element.value == 'famAccoAcctNo1' || element.value == 'famAccoAcctNo2' || element.value == 'famAccoAcctNo3' || element.value == 'famAccoAcctNo4' || element.value == 'famAccoAcctNo5') {
                  this.fieldHeaderList2.push(objTemp);
                } else {
                  this.fieldHeaderList1.push(objTemp);
                }
              }
            }
          });
          console.log(this.fieldHeaderList2);
          console.log(this.fieldHeaderList1);
        }
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
  }
}
