import { Component, OnInit } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_CUS_UPLOAD } from 'app/@uisftech/common/service/http.constant';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";

@Component({
  selector: 'deposit-product-flexible-pricing',
  templateUrl: './deposit-product-flexible-pricing.component.html',
  styleUrls: ['./deposit-product-flexible-pricing.component.scss']
})

export class DepositProductPlexiblePricingComponent implements OnInit {

  selectForm: FormGroup;  // 查询表单
  tabTable: any = []    // 查询结果
  loadingShow: any = { flag: false }; // loading框
  pageSize: string = '10';//每页条数
  startIndex: string = '1';//当前页数
  totalNum: number;
  first: number = 0;
  dialogDisplay:boolean = false;
  msgs: Message[] = [];
  inValueItem:any;
  dataObj: any[];
  famliyDisplay: boolean = false;
  currencyCode : any = [
    {label:'请选择',value:''},
    {label:'人民币',value:'CNY'},
    {label:'英镑',value:'GBP'},
    {label:'港币',value:'HKD'},
    {label:'美元',value:'USD'},
    {label:'日元',value:'JPY'},
    {label:'欧元',value:'EUR'}
  ];
  status : any = [
    {label:'请选择',value:''},
    {label:'已保存',value:'0'},
    {label:'已否决',value:'3'},
    {label:'已签约',value:'2'},
    {label:'待复核',value:'1'},
    {label:'已解约',value:'4'},
  ];


  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    private fb: FormBuilder,
    private messageService: MessageService,
    private commonFunc: Customtools,
  ) {

  }

  ngOnInit() {
    this.selectForm = this.fb.group({
      'custId': [''],// 客户号
      'custName': [''],// 客户名称
      'accountNo': [''],// 存款账户号
      'currency': [''],// 币种
      'status': [''],// 状态
    });
    this.query();

  }

  // 点击查询
  doquery() {
    this.first = 0;
    this.startIndex = '1';
    this.query();
  }
  // 重置
  reset() {
    this.selectForm.reset();
    this.query();
  }

  分页
  paginate(event) {
    // 每页显示的条数;
    this.pageSize = event.rows;
    // 当前页
    this.startIndex = event.page + 1 + '';
    this.first = event.first;
    // 调用查询的方法
    this.query();
  }


  query(isMessage?) {
    const params = {
      pageSize: this.pageSize,
      startIndex: this.startIndex
    }
    for (var key in this.selectForm.value) {
      if (this.selectForm.value[key]) {
          params[key] = this.selectForm.value[key]
      }
  }
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: !isMessage,
      inputParam: params,
      message: this.messageService,
      injectName: 'queryFlexibleDepositInterest',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        console.log(bodyData)
        this.tabTable = bodyData.resultList;
        this.totalNum = bodyData.totalNum;
      }
    });
  }

  add(){
    this.dialogDisplay = true;
  }

  call(event){
    this.dialogDisplay = false;
    this.inValueItem = '';
    if(event){
      this.query(true);
    }
  }

  onHide(){
    this.inValueItem = '';
  }

  toDetail(data){
    this.inValueItem = data.accountNo;
    this.dialogDisplay = true;
  }

  toSee(col){
    console.log(col);
    //外币
    if(col.currency && (col.currency == 'GBP' || col.currency == 'HKD' || col.currency == 'USD' || col.currency == 'EUR' || col.currency == 'JPY')){
      /**
       * 家庭展示亲情号码
       * 0 单户 不展示  
       * 1 家庭 展示
       */
      if(col.countType && col.countType == '1'){
        this.dataObj = col;
        this.famliyDisplay = true;
      }
    }
  }

   //按钮权限
   permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
}
}


