import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_CUS_UPLOAD } from 'app/@uisftech/common/service/http.constant';

@Component({
  selector: 'add-deposit-product',
  templateUrl: './add-deposit-product.component.html',
  styleUrls: ['./add-deposit-product.component.scss']
})

export class AddDepositProductComponent implements OnInit {
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  @Input() inValueItem: any;

  tabMesg: any = tableMessage;
  msgs: Message[] = [];
  cardNo: string;
  num: string;
  accountNo: string;
  custId: string;
  custName: string;
  currency: string;
  countType: string;
  productTypeList: any;
  executeRate: string;
  executeRate2: string;
  productType: string;
  floatRatio: string;
  floatPoint: string;
  depositAmount: string;
  applyReason: string;
  appointAverageDaily: string;
  accountProductTypeLocal: string;
  executeRateLocal: string;
  appointAverageDailyRateLocal: string;
  appointAverageDailyLocal: string;
  appointAverageDailyRate: string;
  bigCustMark: string;
  file: string;
  bigCustMarkFlag: any = [
    { label: '请选择', value: '' },
    { label: '是', value: '1' },
    { label: '否', value: '0' }
  ]
  accountProductType: string;//账户产品类型
  acctSerialNo: string;
  flag1: boolean = false;
  flag2: boolean = false;
  dialogDisplay: boolean = false;
  display1: boolean = false;
  display2: boolean = false;
  display3: boolean = false;
  display4: boolean = false;
  display5: boolean = false;
  display6: boolean = false;
  display7: boolean = false;
  display8: boolean = false;
  display9: boolean = false;
  uploadDisplay: boolean = false;
  UPLOAD: string = TZB_CUS_UPLOAD;
  fileList: any;
  filePath: string;
  fileName: string;
  readonly2: boolean = false;
  disabled1: boolean = false;
  dealPerShow: boolean = false;
  waitSelUserList: any;
  outVal: any;
  bussinessMap: any = {
    amountLevel: '',
    amountLevelMode: ''
  };
  countTypeList: any = [
    { label: '单户', value: '0' },
    { label: '家庭', value: '1' }
  ];
  familyAccountDisplay: boolean = false;
  famAccoAcctNo1: string = '';
  famAccoAcctNo2: string = '';
  famAccoAcctNo3: string = '';
  famAccoAcctNo4: string = '';
  famAccoAcctNo5: string = '';
  famAccoAcctNo1ASN: string = '';
  famAccoAcctNo2ASN: string = '';
  famAccoAcctNo3ASN: string = '';
  famAccoAcctNo4ASN: string = '';
  famAccoAcctNo5ASN: string = '';
  familyIdentification: boolean = false;
  currencyLabel: string;
  currencyValue: string;
  currencyCode: any = [
    { label: '请选择', value: '' },
    { label: '人民币', value: 'RMB' },
    { label: '英镑', value: 'GBP' },
    { label: '港币', value: 'HKD' },
    { label: '美元', value: 'USD' },
    { label: '日元', value: 'JPY' },
    { label: '欧元', value: 'EUR' }
  ];
  // isAddFlag : boolean = false;
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc
  ) {

  }

  ngOnInit() {
    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (s.extParam.specifyNextOpers || s.extParam.bussinessMap) {
      delete s.extParam.specifyNextOpers
      delete s.extParam.bussinessMap
      let commonHeader = s;
      this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    }
    if (this.inValueItem) {
      this.accountNo = this.inValueItem;
      this.flag1 = true;
      this.queryDepositAccountInfo();
    } else {
      this.accountNo = '';
      this.flag1 = false;
    }
  }

  productChange() {
    this.accountProductType = this.accountProductTypeLocal;
    this.executeRate = this.executeRateLocal;

    this.appointAverageDailyRate = this.appointAverageDailyRateLocal;
    this.appointAverageDaily = this.appointAverageDailyLocal;
    this.currency = this.currencyValue;
    this.floatPoint = '';
    this.floatRatio = '';
    if (this.productTypeList.length > 1) {
      this.returnFalse();
      if (this.currency == 'CNY') {
        this.readonly2 = false;
        this.disabled1 = false;
        this.display8 = true;
        this.display9 = false;
        switch (this.accountProductType) {
          case '1'://201活期科目
          case '4'://活期保证金
            if (this.productType == '3') {
              this.returnTrue([1, 2, 7]);
            } else if (this.productType == '4') {
              this.readonly2 = true;
              this.returnTrue([1, 2, 3, 4, 7]);
            } else if (this.productType == '1') {
              this.returnTrue([1, 2, 5, 6, 7]);
              this.executeRate = this.appointAverageDailyRate;
              this.executeRate2 = this.executeRate;
              this.disabled1 = true;
            } else if (this.productType == '2') {
              this.returnTrue([]);
            }
            break;
          case '2'://215定期科目
          case '5'://定期保证金
            if (this.productType == '3') {
              this.returnTrue([1, 2, 7]);
              this.executeRate2 = this.executeRate;
            } else if (this.productType == '1') {
              this.disabled1 = true;
              this.executeRate = this.appointAverageDailyRate;
              this.returnTrue([1, 2, 5, 6, 7]);
              this.executeRate2 = this.appointAverageDailyRateLocal;
            } else if (this.productType == '2') {
              this.readonly2 = true;
              this.returnTrue([]);
            }
            break;
          case '3'://215定期财运通科目
            // if(this.productType == '1'){
            //   this.returnTrue([1, 2, 3, 4, 7]);
            // }else if(this.productType == '4'){
            //   this.returnTrue([1, 2, 3, 4, 7]);
            // }
            this.readonly2 = true;
            this.returnTrue([1, 3, 4]);
            break;
        }
      } else {//外币
        this.returnTrue([2]);
      }
    }
  }

  countTypeChange() {
    if (this.countType && this.countType == '1') {
      this.familyAccountDisplay = true;
    } else {
      this.familyAccountDisplay = false;
      this.famAccoAcctNo1 = '';
      this.famAccoAcctNo2 = '';
      this.famAccoAcctNo3 = '';
      this.famAccoAcctNo4 = '';
      this.famAccoAcctNo5 = '';
      this.famAccoAcctNo1ASN = '';
      this.famAccoAcctNo2ASN = '';
      this.famAccoAcctNo3ASN = '';
      this.famAccoAcctNo4ASN = '';
      this.famAccoAcctNo5ASN = '';
    }
  }

  float() {
    if (this.display3 && this.display4) {
      //利率*（1+浮动比例/100）+上浮点数/100
      if (this.executeRate && this.floatRatio && this.floatPoint) {
        if (Number(this.executeRate) + '' == 'NaN' || Number(this.floatRatio) + '' == 'NaN' || Number(this.floatPoint) + '' == 'NaN') {
          return
        }
        this.appointAverageDailyRate = this.executeRate;
        this.executeRate2 = parseFloat(this.executeRate) * (1 + parseFloat(this.floatRatio) / 100) + parseFloat(this.floatPoint) / 100 + '';
        console.log(this.executeRate2);
      }
    } else {
      this.executeRate2 = this.executeRate;
    }

  }

  query() {
    if (this.accountNo) {
      if (this.accountNo == '') {
        this.flag2 = false;
      } else {
        this.queryDepositAccountInfo();
      }
    }
  }

  keydown(event) {
    let e = event || window.event;
    console.log(e.keyCode);
    if (e && e.keyCode == 13) {//enter 键盘事件
      this.dialogDisplay = true;
      this.selectImportHoldDepositInfo();
    }
  }

  showDialog(i) {
    if (i == 2) {
      this.uploadDisplay = true;
    }
  }

  queryDepositAccountInfo() {
    let params = {
      accountNo: this.accountNo,
      acctSerialNo: this.acctSerialNo,
    }
    this.httpService.queryDepositAccountInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.returnFalse();
        this.bigCustMark = '';
        this.productType = '';
        this.flag2 = true;
        this.custId = data.custId;
        this.custName = data.custName;
        this.productTypeList = data.productTypeList;
        if (data.productTypeList && data.productTypeList.length != 0) {
          this.productTypeList = this.transList(data.productTypeList);
        }
        this.accountProductTypeLocal = data.accountProductType;
        this.executeRateLocal = data.executeRate;
        this.appointAverageDailyRateLocal = data.appointAverageDailyRate;
        this.appointAverageDailyLocal = data.appointAverageDaily;
        this.currencyValue = data.currency;
        //外币
        if (data.currency && (data.currency == 'GBP' || data.currency == 'HKD' || data.currency == 'USD' || data.currency == 'EUR' || data.currency == 'JPY')) {
          this.familyIdentification = true;
          this.countType = '0';
          this.display8 = false;
          this.display9 = true;
          this.currencyCode.forEach(el => {
            if (el.value == this.currencyValue) {
              this.currencyLabel = el.label
            }
          });
          // this.currency = data.currency;
        } else {
          this.familyIdentification = false;
          this.countType = '';
          this.famAccoAcctNo1 = '';
          this.famAccoAcctNo2 = '';
          this.famAccoAcctNo3 = '';
          this.famAccoAcctNo4 = '';
          this.famAccoAcctNo5 = '';
          this.famAccoAcctNo1ASN = '';
          this.famAccoAcctNo2ASN = '';
          this.famAccoAcctNo3ASN = '';
          this.famAccoAcctNo4ASN = '';
          this.famAccoAcctNo5ASN = '';
        }
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
  }

  selectImportHoldDepositInfo() {
    let params = {
      custNo: this.custId
    }
    this.httpService.selectImportHoldDepositInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.flag1 = true;
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
  }

  transList(list: any[]) {
    let temp = [{ label: '请选择', value: '' }];
    for (let k in list) {
      let tempObj = { label: '', value: '' };
      tempObj['value'] = k;
      tempObj['label'] = list[k];
      temp.push(tempObj);
    }
    return temp
  }

  returnFalse() {
    this.display1 = false;
    this.display2 = false;
    this.display3 = false;
    this.display4 = false;
    this.display5 = false;
    this.display6 = false;
    this.display7 = false;
  }

  returnTrue(list: any[]) {
    if (list && list.length != 0) {
      list.forEach(element => {
        this[`display${element}`] = true;
      });
    }
  }

  // 上传点击,需跨域
  onBeforeSend(event) {
    if (event.xhr.response) {
      this.fileList = JSON.parse(event.xhr.response);
      if (this.fileList.hasOwnProperty('errorMsg')) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
        return
      }
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功' });
      this.filePath = this.fileList[0].fileUrl;
      this.fileName = this.fileList[0].fileName;
      this.file = this.fileList[0].fileName;
    }
    else {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
    }
  }

  submit1(i) {
    debugger
    if (i == '0') {//保存
      this.submit2(i);
    } else {
      let floatPoint;
      if (this.floatPoint) {
        floatPoint = this.floatPoint
      } else {
        floatPoint = '0';
      }
      let params = {
        custId: this.custId,
        custName: this.custName,
        currency: this.currency,
        accountNo: this.accountNo,
        accountProductType: this.accountProductType,
        productType: this.productType,
        depositAmount: this.depositAmount,
        floatPoint: floatPoint,
        floatRatio: this.floatRatio,
        appointAverageDaily: this.appointAverageDaily,
        // executeRate: this.executeRate2,
        isProductRatio: '0',
        bigCustMark: this.bigCustMark,
        applyReason: this.applyReason,
        filePath: this.filePath,
        fileName: this.fileName,
        operationType: i,
        acctSerialNo: this.acctSerialNo,
      }
      this.executeRate2 ? params['executeRate'] = this.executeRate2 : params['executeRate'] = this.executeRate;
      this.countType ? params['countType'] = this.countType : params['countType'] = '';
      this.httpService.queryFlexibleDepositOrgPost(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.bussinessMap.amountLevel = data.amountLevel;
          this.bussinessMap.amountLevelMode = data.amountLevelMode;
          this.waitSelUserList = this.commfunc.query('CKZH_LHJX', { amountLevel: data.amountLevel, amountLevelMode: data.amountLevelMode });//选人
          if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            let chooseThis = this.waitSelUserList.display;
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            let a = commonHeader.extParam.specifyNextOpers;
            if (chooseThis == true && (a == undefined || a == '' || a == null)) {
              this.dealPerShow = true;
            } else {
              this.submit2(i);
            }
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
          }
        } else {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
      }, err => {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
      });
    }
  }

  submit2(i) {
    let floatPoint;
    if (this.floatPoint) {
      floatPoint = this.floatPoint
    } else {
      floatPoint = '0';
    }
    let tempObj = {};
    for (let i = 1; i <= 5; i++) {
      tempObj[`famAccoAcctNo${i}`] = this[`famAccoAcctNo${i}`];
      tempObj[`famAccoAcctNo${i}ASN`] = this[`famAccoAcctNo${i}ASN`];
    }

    let params = {
      custId: this.custId,
      custName: this.custName,
      currency: this.currency,
      accountNo: this.accountNo,
      accountProductType: this.accountProductType,
      productType: this.productType,
      depositAmount: this.depositAmount,
      floatPoint: floatPoint,
      floatRatio: this.floatRatio,
      appointAverageDaily: this.appointAverageDaily,
      // executeRate: this.executeRate2,
      isProductRatio: '0',
      bigCustMark: this.bigCustMark,
      applyReason: this.applyReason,
      filePath: this.filePath,
      fileName: this.fileName,
      operationType: i,
      acctSerialNo: this.acctSerialNo,
    }
    Object.assign(params, tempObj);
    console.log(params);
    this.executeRate2 ? params['executeRate'] = this.executeRate2 : params['executeRate'] = this.executeRate;
    this.countType ? params['countType'] = this.countType : params['countType'] = '';
    this.httpService.createFlexibleDepositInterest(params).subscribe(data => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
      }
      if (s.bussinessMap) {
        delete s.bussinessMap
      }
      let commonHeader = s;
      this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      if (data.returnCode.code == 'success') {
        this.uploadDisplay = false;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '调用成功' });
        this.outValueEmit.emit(true);
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
  }

  //
  outDisplayCall(event) {
    this.dealPerShow = event;
  }

  //选人回调
  outValueCall(event) {
    console.log(event);
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    }); debugger
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    commonHeader['bussinessMap'] = this.bussinessMap;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.submit2('1');
  }
}
